import { useEffect, useState } from "react";
import { fetchImages } from "./api";



function Header() {
    return (
      <header className="hero is-white is-bold">
        <div className="half-body">
          <div className="container">
            <h1 className="title"><font face="Impact">~Dog~</font></h1>
          </div>
          <h2>Áä¨„ÅÆÁîªÂÉè„ÅåÊ§úÁ¥¢„Åß„Åç„Çã„Çµ„Ç§„Éà„Åß„Åô„ÄÇ</h2>
        </div>
      </header>
    );
  }
  
  function Image(props) {
   return (
      <div className="photo">
        <div className="photo-image">
          <figure className="image">
          <img src={props.src} alt="dog" />
          </figure>
        </div>
      </div>
    );
  }
  
  function Loading() {
     return <p><font face="Impact">Ë™≠„ÅøËæº„Åø‰∏≠„Åß„Åô„ÄÇÂ∞ë„ÄÖ„ÅäÂæÖ„Å°„Åè„Å†„Åï„ÅÑ„ÄÇ</font></p>;
    }

  function Gallery(props) {
      const { urls } = props;
      if (urls == null) {
            return <Loading />;
          }  
    return (
      <div className="columns is-vcentered is-multiline">
         {urls.map((url) => {
        return (
          <div key={url} className="column is-3">
            <Image src={url} />
          </div>
        );
      })}
      </div>
    );
  }

 

  function Form(props) {
      function handleSubmit(event) {
        event.preventDefault();
        const { breed } = event.target.elements;
        props.onFormSubmit(breed.value);
      }
      return (
        <div>
          <form onSubmit={handleSubmit}>
            <div className="field has-addons">
              <div className="control is-expanded">
                <div className="select is-halfwidth">
                  <select name="breed" defaultValue="husky">
                    <option value="shiba">Êü¥Áä¨</option>
                    <option value="akita">ÁßãÁî∞Áä¨</option>
                    <option value="beagle">„Éì„Éº„Ç∞„É´</option>
                    <option value="husky">„Éè„Çπ„Ç≠„Éº</option>
                    <option value="boxer">„Éú„ÇØ„Çµ„ÉºÁä¨</option>
                    <option value="borzoi">„Éú„É´„Çæ„Ç§</option>
                    <option value="beagle">„Éì„Éº„Ç∞„É´</option>
                    <option value="doberman">„Éâ„Éº„Éô„É´„Éû„É≥</option>
                    <option value="maltese">„Éû„É´„ÉÅ„Éº„Ç∫</option>
                    <option value="mix">„Éü„ÉÉ„ÇØ„ÇπÁä¨</option>
                    <option value="affenpinscher">„Ç¢„Éº„Éï„Çß„É≥„Éî„É≥„Ç∑„É£„Éº</option>
                    <option value="african">„Ç¢„Éï„É™„Ç´„É≥</option>
                    <option value="airedale">„Ç®„Ç¢„Éá„Éº„É´</option>
                    <option value="appenzeller">„Ç¢„ÉÉ„Éö„É≥„ÉÑ„Çß„É´„ÉÅ„Éº„Ç∫</option>
                    <option value="basenji">„Éê„Çª„É≥„Ç∏„Éº</option>
                    <option value="brabancon">„Éó„ÉÅ„Éª„Éñ„É©„Éê„É≥„ÇΩ„É≥</option>
                    <option value="briard">„Éñ„É™„Ç¢„Éº„Éâ</option>
                    

                    
                  </select>
                </div>
              </div>
              <div className="control">
                <button type="submit" className="button is-black">
                  üê∂
                </button>
              </div>
            </div>
          </form>
        </div>
      );
    }
    
  
  function Main() {
    const [urls, setUrls] = useState(null);
    useEffect(() => {
            fetchImages("husky").then((urls) => {
                setUrls(urls);
            });
          }, []);
          function reloadImages(breed) {
                fetchImages(breed).then((urls) => {
                  setUrls(urls);
                });
              }
              
          return (
          
      <main>
          <section className="section">
            
        <div className="container">
        <Form onFormSubmit={reloadImages} />
        </div>
      </section>
        <section className="section">
          <div className="container">
          <Gallery urls={urls} />
          </div>
        </section>
      </main>
    );
  }
  
  function Footer() {
    return (
      <footer className="footer">
        <div className="content has-text-centered">
          <p>Dog images are retrieved from Dog API</p>
          <p>
            <a href="https://dog.ceo/dog-api/about">Donate to Dog API</a>
          </p>
        </div>
      </footer>
    );
  }
  
  function App() {
    return (
      <div>
        <Header />
        <Main />
        <Footer />
      </div>
    );
  }
  
  export default App;